.p-webgl{
    max-width: 480px;
    width: 100%;
    height: auto;
    position: fixed;
}
.p-webgl-inner{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding-bottom: 56.25%; /* 16:9 のアスペクト比を維持するためのトリック */
    height: 0;
    overflow: hidden;
    canvas{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}
.wrap{
    display: flex;
}
.list-images{
    width: 200px;
    img{
        width: 100%;
        height: 400px;
        display: block;
    }
}